.overlay-exit-intervention {
  visibility: visible;
  z-index: 4;
  opacity: 1;

  .overlay-wrap {
    transform: scale(1);
    max-width: 960px;
    width: 960px;

    .cancel {
      display: none;
    }

    .overlay-content {
      // width: 100%;
    }

    .overlay-content-container {
      display: flex;
      padding: 0;
      min-height: 54vh;
      overflow: hidden;
      background-color: #fff8f8;

      .overlay-image-container {
        padding: 0;
        width: 62.5%;
        // overflow: hidden;
      }

      .overlay-text-container {
        width: 37.5%;
        // background-color: #fff8f8;
        text-align: center;
        margin: 70px auto 0;
        padding: 0 25px 20px;

        p {
          color: #000;
          font-size: 16px;
          margin-bottom: 12px;

          &.xs {
            font-size: 12px;
            margin-bottom: 0;
          }

          &.light-grey {
            color: #9e9e9e;
          }

          &.padding-xs-top {
            padding-top: 10px;
          }
        }

        h1 {
          font-size: 32px;
          font-weight: 500;
          margin-bottom: 12px;
          color: #000;
        }

        h3 {
          font-size: 24px;
          font-weight: 500;
          margin-bottom: 0;
          line-height: 140%;
        }

        .cta {
          letter-spacing: 0;
          width: 185px;
          height: 40px;
          border-radius: 40px;
          font-size: 16px;
          padding-top: 5px;
        }

        .overlay-cta-container {
          width: 100%;
          padding-top: 30px;
          padding-bottom: 70px;
        }

        .dismiss {
          font-size: 12px;
          margin-bottom: 10px;
          border-bottom: 1px solid #9e9e9e;
          color: #9e9e9e;
          text-decoration: none;
        }

        .price {
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 24px;
          font-weight: 500;
          color: #a50034;
          sup {
            top: 0;
            font-size: 24px;
          }

          span {
            padding-top: 5px;
            font-size: 12px;
            font-weight: 400;
            color: #9E9E9E;
          }
        }
      }
    }
  }
}